import React from "react";
import { Route, Switch } from "react-router-dom";
import "react-modal-video/css/modal-video.min.css";
import AsyncRoute from "./AsyncRoute";

const App = () => (
	<Switch>
		<Route
			exact
			path="/"
			component={props => (
				<AsyncRoute
					props={props}
					loadingPromise={import("./PopularMovies")}
				/>
			)}
		/>
		<Route
			exact
			path="/movie/:id"
			component={props => (
				<AsyncRoute
					props={props}
					loadingPromise={import("./MovieDetails")}
				/>
			)}
		/>
	</Switch>
);

export default App;


